
* {
	padding: 0;
	margin: 0;
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
body {
	background: black;
	overflow: hidden;
}
h1 {
	color: white;
	text-align: center;
	margin: 24px 0;
}
.carousel__text {
	position: absolute;
	left: 0;
	right: 0;
	text-align: center;
	top: 50%;
	color: white;
	z-index: 999;
}
.carousel__img {
	opacity: 0.8;
}
.buttons {
	margin-top: 8px;
	text-align: center;
}
.buttons__button{
	border: none;
	padding: 10px 15px;
	text-transform: uppercase;
	cursor: pointer;
	-webkit-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out;
}
.buttons__button:hover {
	color: white;
	background-color: black;
}

.carousel {position: relative;width: 630px;margin: auto;overflow: hidden;
}
.carousel__nav{
	color: white;
	border: none;
	background: transparent;
	position: absolute;
	top: 50%;
	font-size: 50px;
	cursor: pointer;
}
.carousel__prev{
	left: 10px;
}
.carousel__next{
	right: 10px;
}
.carousel__pagination{
	position: absolute;
	bottom: 10px;
	left: 0;
	right: 0;
	text-align: center;
}
.carousel__pagination button {
	border: none;
	display: inline-block;
	width: 10px;
	height: 10px;
	background-color: black;
	opacity: 0.8;
	border-radius: 10px;
	margin: 0 2px;
}
.carousel__pagination button.active {
	background-color: #fff;
}

.slide-right-enter-active {
	-webkit-animation: slideRightIn 1s;
	        animation: slideRightIn 1s
}
@-webkit-keyframes slideRightIn{
from {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
}
to {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
}
}
@keyframes slideRightIn{
from {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
}
to {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
}
}
.slide-right-leave-active {
	-webkit-animation: slideRightOut 1s;
	        animation: slideRightOut 1s;
	position: absolute;
	top: 0;
	left: 0;
}
@-webkit-keyframes slideRightOut{
from {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
}
to {
		-webkit-transform: translateX(-100%);
		        transform: translateX(-100%);
}
}
@keyframes slideRightOut{
from {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
}
to {
		-webkit-transform: translateX(-100%);
		        transform: translateX(-100%);
}
}
.slide-left-enter-active {
	-webkit-animation: slideLeftIn 1s;
	        animation: slideLeftIn 1s
}
@-webkit-keyframes slideLeftIn{
from {
		-webkit-transform: translateX(-100%);
		        transform: translateX(-100%);
}
to {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
}
}
@keyframes slideLeftIn{
from {
		-webkit-transform: translateX(-100%);
		        transform: translateX(-100%);
}
to {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
}
}
.slide-left-leave-active {
	-webkit-animation: slideLeftOut 1s;
	        animation: slideLeftOut 1s;
	position: absolute;
	top: 0;
	left: 0;
}
@-webkit-keyframes slideLeftOut{
from {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
}
to {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
}
}
@keyframes slideLeftOut{
from {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
}
to {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
}
}
